<template>
  <div class="maillclass">
    <mt-header fixed title="通讯录分类">
        <router-link to="/maill" slot="left">
            <mt-button icon="back"></mt-button>
        </router-link>
    </mt-header>
    <!-- 通讯录分类 -->
    <div class="maillclass" v-for="item in maillclass" :key="item.index" @click="getid(item.classid)">
        <h4>{{item.classname}}</h4>
        <!-- <p>{{item.classid}}</p> -->
    </div>
  </div>
</template>

<script>
export default {
  name: 'maillclass',
  data () {
    return {
      msg: '通讯录分类页面',
      maillclass:[]
    }
  },
  created(){
    this.axios.get('http://jmpotal.hanweb.com/jmp/interfaces/numsensecates.do', {
          params: {
            siteid:1,
          }
        })
          .then( res => {
            var data = res.data
            this.maillclass = data
        })
          .catch(function (error) {
          console.log(error);
        });
      },
  methods:{
    getid(classid) {
      localStorage.setItem("classid",classid)
      this.$router.push({path: '/mailllist'})
    }
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
    /* 顶部 */
    .mint-header.is-fixed {
        font-size: 18px;
        height: 44px;
        background-color: rgb(0, 122, 255);
    }
</style>



