<template>
    <!-- 顶部 -->
  <div class="mymaill">
    <div class="mymaill_top">
         <a href="#"><h4></h4></a>
         <div class="user">
             <a href="#"></a>
         </div>
    </div>
    <!-- <h4>{{$store.state.count}}</h4> -->
    <!-- 返回按钮 -->
    <div class="left">
        <mt-header fixed title="">
            <router-link to="" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
        </mt-header>
    </div>
    <!-- 信息列表 -->
     <div class="application">
              <ul>
                  <li>
                      <a href="#">
                          <h3><img src="../assets/maill/gudingdianhua.png">固定电话</h3>
                      </a>
                  </li>
                <li>
                  <a href="#">
                      <h3><img src="../assets/maill/yidongdianhua.png">移动电话</h3>
                  </a>
                </li>

                <li>
                  <a href="#">
                      <h3><img src="../assets/maill/youxiang.png">电子邮箱</h3>
                  </a>
                </li>
                <li>
                    <a href="#" >
                      <h3><img src="../assets/maill/guanfangwangzhan.png">官方网站</h3>
                    </a>
                </li>

                <li>

                <a href="#" >
                      <h3><img src="../assets/maill/gongsijianjie.png">简介内容</h3>
                    </a>
                  </li>
              </ul>
        </div>

  </div>
</template>

<script>
export default {
  name: 'mymaill',
  data () {
    return {
      msg: '我的通讯录页面'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
    /* content_top 开始 */
        .mymaill_top {
            height: 210px;
            width: 100%;
            background-image: url(../assets/maill/firstul4.jpg);
            background-size: 100% 100%;
            position: relative;
            margin-top: -60px;
        }
        .mymaill_top a h4 {
            text-align: center;
            position: absolute;
            bottom: 28px;
            left: 50%;
            margin-left: -36px;
            color: #fff;
        }
        .mymaill_top .user {
            width: 100px;
            height: 100px;
            background-color: #fff;
            border-radius: 50%;
            position: absolute;
            left: 50%;
            margin-left: -50px;
            top: 40px;
            overflow: hidden;
        }
        .mymaill_top .user a {
            display: inline-block;
            width: 100%;
            height: 100%;
        }
        /* 返回按钮 */
        .mint-header.is-fixed {
        font-size: 18px;
        height: 44px;
        /* background-color: rgb(0, 122, 255); */
        background-color: transparent;
    }
    /* 信息列表 */
    .application {
        padding: 10px;
    }
    .application ul {
    overflow: hidden;
    /* float: left; */
  }
  .application ul li {
    background-color: #fff;
    float: left;
    width: 100%;
    list-style: none;
    border: 1px solid #FEFEFE;
    height: 55px;
    position: relative;
    margin: 4px 3px;
  }
  .application ul li img {
    position: absolute;
    top: 9px;
    left: 22px;
    width: 30px;
    height: 30px;
  }
  .application ul li a {
    display: inline-block;
    width: 100%;
    text-decoration: none;
    padding-left: 55px;
  }
  
  .application ul li h3 {
    text-align: center;
    line-height: 55px;

  }
  .mui-h3, h3 {
    text-align: left;
    font-size: 1rem;
    padding-left: 50px;
  }
  .application h3:nth-child(1) {
    height: 50px;
    padding-left: 5px;
    line-height: 15px;
    color: #000;
    font-weight: 100;
    font-size: 18px;
    text-align: left;
  }
</style>



