<template>
  <div class="maillist">
    <!-- 顶部 -->
    <mt-header fixed title="通讯录">
        <router-link to="/maillclass" slot="left">
            <mt-button icon="back"></mt-button>
        </router-link>
    </mt-header>
    <!-- 导航 -->
    <mt-navbar class="page-part" v-model="selected">
      <mt-tab-item id="1">服务热线</mt-tab-item>
      <mt-tab-item id="2">星标号码</mt-tab-item>
    </mt-navbar>
    <!-- tab-container -->
    <mt-tab-container v-model="selected" >
      <mt-tab-container-item id="1" v-for="(item,index) in maill" :key="index">
        <ul>
          <li>姓名：{{item.classname}}</li>
          <li>手机号：{{item.mobilephone}}</li>
          <li>固定电话：{{item.fixedphone}}</li>
          <li>邮箱：{{item.email}}</li>
        </ul>
      </mt-tab-container-item>
      <mt-tab-container-item id="2">
        2
      </mt-tab-container-item>
    </mt-tab-container>
  </div>
</template>

<script>
export default {
  name: 'maillist',
  data () {
    return {
      msg: '我是通讯录页面',
      selected:'1',
      value:"",
      maill:[]
    }
  },
  created(){
    this.axios.get('http://jmpotal.hanweb.com/jmp/interfaces/numsensedetail.do', {
          params: {
            siteid:1,
            cateid:1
          }
        })
          .then( res => {
            var data = res.data
            this.maill.push(data)
            console.log(this.maill)
        })
          .catch(function (error) {
          console.log(error);
        });
      },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: block;
  /* display: inline-block; */
  margin: 0 10px;
}
.maillist{
    background-color: #f2f2f2;
    /* height: 500px; */
    margin-top: -15px;
  }
  /* 顶部 */
  .mint-header.is-fixed {
    font-size: 18px;
    height: 44px;
    background-color: #007AFF;
  }
  /* 导航 */
  .maillist .mint-navbar {
        height: 40px;
        font-size: 16px;
        border-bottom: 1px solid #c9c9c9;
        width: 100%;
  }
  
  
</style>



