import Vue from 'vue'
import Router from 'vue-router'
import maill from '../project/maill.vue'
import maillclass from '../project/maillclass.vue'
import mailllist from '../project/mailllist.vue'
import mymaill from '../project/mymaill.vue'

Vue.use(Router)

export default new Router({
  model:history,
  routes: [
    {
      path: '/',
      name: 'maill',
      component: maill
    },
    {
      path: '/maill',
      name: 'maill',
      component: maill
    },
    {
      path: '/maillclass',
      name: 'maillclass',
      component: maillclass
    },
    {
      path: '/mailllist',
      name: 'mailllist',
      component: mailllist
    },
    {
      path: '/mymaill',
      name: 'mymaill',
      component: mymaill
    },
  ]
})



