<template>
  <div class="mailllist">
    <mt-header fixed title="通讯录列表">
        <router-link to="/maillclass" slot="left">
            <mt-button icon="back"></mt-button>
        </router-link>
    </mt-header>

    <!-- 通讯录列表 -->
    <div class="maillclass" v-for="item in mailllist" :key="item.index">
        <h4>{{item.classname}}</h4>
    </div>
  </div>
</template>

<script>
export default {
  name: 'mailllist',
  data () {
    return {
      msg: '通讯录分类页面',
      mailllist:[]
    }
  },
  created(){
    var classid = localStorage.getItem("classid")
    this.axios.get('http://jmpotal.hanweb.com/jmp/interfaces/numsenselist.do', {
          params: {
            siteid:1,
            cateid:classid,
          }
        })
          .then( res => {
            var data = res.data
            this.mailllist = data
        })
          .catch(function (error) {
          console.log(error);
        });
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
    /* 顶部 */
    .mint-header.is-fixed {
        font-size: 18px;
        height: 44px;
        background-color: rgb(0, 122, 255);
    }
</style>



